import { DEFAULT_VIDEO_CONSTRAINTS } from '../../../constants';
import { useCallback, useEffect, useState } from 'react';
import Video, { LocalVideoTrack, LocalAudioTrack, CreateLocalTrackOptions } from 'twilio-video';

export default function useLocalTracks() {
  const [audioTrack, setAudioTrack] = useState<LocalAudioTrack>();
  const [videoTrack, setVideoTrack] = useState<LocalVideoTrack>();
  const [isAcquiringLocalTracks, setIsAcquiringLocalTracks] = useState(false);

  const getLocalAudioTrack = useCallback((deviceId?: string) => {
    console.log('THIS SHOULDNT BE CALLED');
    const options: CreateLocalTrackOptions = {};

    const urlParams = new URLSearchParams(window.location.search);
    const videoId = urlParams.get('video-id');

    if (videoId) {
      console.log('before video stream:');
      console.log(videoId);
      const video = (document.getElementById(videoId) as HTMLMediaElement);
      console.log('playing');
      video.play();
      console.log('done starting playing');
      var videoStream = (document.getElementById(videoId) as HTMLMediaElement).captureStream();
      console.log('video stream:');
      console.log(videoStream);

      const localAudioTrack = new Video.LocalAudioTrack(videoStream.getAudioTracks()[0]);
      //const localAudioTrack = new Video.LocalAudioTrack(videoStream.getAudioTracks()[0]);

      console.log(localAudioTrack);

      setAudioTrack(localAudioTrack);
    
      return Promise.resolve(localAudioTrack);
    }
    if (deviceId) {
      options.deviceId = { exact: deviceId };
    }

    return Video.createLocalAudioTrack(options).then(newTrack => {
      setAudioTrack(newTrack);
      return newTrack;
    });
  }, []);

  const getLocalVideoTrack = useCallback((newOptions?: CreateLocalTrackOptions) => {
    const urlParams = new URLSearchParams(window.location.search);
    const videoId = urlParams.get('video-id');

    if (videoId) {
      console.log('before video stream:');
      console.log(videoId);
      const video = (document.getElementById(videoId) as HTMLMediaElement);
      console.log('playing');
      video.play();
      video.onended = () => {
        video.currentTime = 0;
        video.play();
      };
      console.log('done starting playing');
      var videoStream = (document.getElementById(videoId) as HTMLMediaElement).captureStream();
      console.log('video stream:');
      console.log(videoStream);

      console.log(videoStream.getVideoTracks().length);
      const localVideoTrack = new Video.LocalVideoTrack(videoStream.getVideoTracks()[0], {logLevel: 'debug', name: `camera-${Date.now()}`});
      const localAudioTrack = new Video.LocalAudioTrack(videoStream.getAudioTracks()[0]);

      console.log('setting local video tracks');
      setAudioTrack(localAudioTrack as LocalAudioTrack);
      setVideoTrack(localVideoTrack as LocalVideoTrack);

      return Promise.resolve(localVideoTrack);

    } else {
      // In the DeviceSelector and FlipCameraButton components, a new video track is created,
      // then the old track is unpublished and the new track is published. Unpublishing the old
      // track and publishing the new track at the same time sometimes causes a conflict when the
      // track name is 'camera', so here we append a timestamp to the track name to avoid the
      // conflict.
      const options: CreateLocalTrackOptions = {
        ...(DEFAULT_VIDEO_CONSTRAINTS as {}),
        name: `camera-${Date.now()}`,
        ...newOptions,
      };

      return Video.createLocalVideoTrack(options).then(newTrack => {
        setVideoTrack(newTrack);
        return newTrack;
      });
    }
  }, []);

  const removeLocalVideoTrack = useCallback(() => {
    if (videoTrack) {
      videoTrack.stop();
      setVideoTrack(undefined);
    }
  }, [videoTrack]);

  /*useEffect(() => {

    const urlParams = new URLSearchParams(window.location.search);
    const videoId = urlParams.get('video-id');

    setIsAcquiringLocalTracks(true);

    if (videoId) {
      console.log('before video stream:');
      console.log(videoId);
      const video = (document.getElementById(videoId) as HTMLMediaElement);
      console.log('playing');
      //video.play();
      console.log('done starting playing');
      var videoStream = (document.getElementById(videoId) as HTMLMediaElement).captureStream();
      console.log('video stream:');
      console.log(videoStream);

      const localVideoTrack = new Video.LocalVideoTrack(videoStream.getVideoTracks()[0], {logLevel: 'debug', name: `video-${Date.now()}`});
      const localAudioTrack = new Video.LocalAudioTrack(videoStream.getAudioTracks()[0], {logLevel: 'debug', name: `audio-${Date.now()}`});

      console.log(localVideoTrack);
      console.log(localAudioTrack);

      setVideoTrack(localVideoTrack);
      setAudioTrack(localAudioTrack);
      setIsAcquiringLocalTracks(false);

    } else {
      Video.createLocalTracks({
        video: {
          ...(DEFAULT_VIDEO_CONSTRAINTS as {}),
          name: `camera-${Date.now()}`,
        },
        audio: true,
      })
        .then(tracks => {
          const videoTrack = tracks.find(track => track.kind === 'video');
          const audioTrack = tracks.find(track => track.kind === 'audio');
          if (videoTrack) {
            setVideoTrack(videoTrack as LocalVideoTrack);
          }
          if (audioTrack) {
            setAudioTrack(audioTrack as LocalAudioTrack);
          }
        })
        .finally(() => setIsAcquiringLocalTracks(false));
    }
  }, []);*/


  const localTracks = [audioTrack, videoTrack].filter(track => track !== undefined) as (
    | LocalAudioTrack
    | LocalVideoTrack
  )[];

  console.log('returning local tracks');
  console.log(localTracks);
  return { localTracks, getLocalVideoTrack, getLocalAudioTrack, isAcquiringLocalTracks, removeLocalVideoTrack };
}
