import React, { useCallback, useState } from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContentText from '@material-ui/core/DialogContentText';
import { TwilioError } from 'twilio-video';
import useLocalTracks from '../VideoProvider/useLocalTracks/useLocalTracks';
import { useAppState } from '../../state';
import useVideoContext from '../../hooks/useVideoContext/useVideoContext';


function ConfirmDialog() {

  const [dismissed, setDismissed] = useState(false);
  
  const {
    getLocalVideoTrack,
  } = useVideoContext();

  const { connect } = useVideoContext();
  const { setError, getToken } = useAppState();

  const onClick = useCallback(() => {
    getLocalVideoTrack();

    const urlParams = new URLSearchParams(window.location.search);

    const room = urlParams.get('room');
    const identity = urlParams.get('identity');

    if (!room || !identity) {
      setError(new Error('Must provide room and identity in url params.') as TwilioError);
      return;
    }

    console.log('getting token');
    getToken(identity, room).then((token) => {
      console.log('connect');
      connect(token);
    }).catch((e) => console.log(e)).finally(() => console.log('finally'));
  
    setDismissed(true);
  }, [getLocalVideoTrack, connect, getToken, setError]);


  return (
    <Dialog open={!dismissed} onClose={() => onClick()} fullWidth={true} maxWidth="xs">
      <DialogTitle>Start Video</DialogTitle>
      <DialogContent>
        <DialogContentText>Launch video stream?</DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={() => onClick()} color="primary" autoFocus>
          OK
        </Button>
      </DialogActions>
    </Dialog>
  );
}

export default ConfirmDialog;
